import React from "react";
import "./App.css";
import Form from "./app_component/form.component";
import Weather from "./app_component/weather.component";
import "bootstrap/dist/css/bootstrap.min.css";
import clear from './assets/sunny.png'
import rain from './assets/rain.png'
import snow from './assets/snow.jpeg'
import clouds from './assets/clouds.jpeg'
import drizzle from './assets/drizzle.jpg'
import atmos from './assets/atmos.png'
import thunderstorm from './assets/thunderstorm.jpg'

// import Style from './VV.css';
// import "weather-icons/css/weather-icons.css";

const Api_Key = "429736441cf3572838aa10530929f7cd";

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      city: undefined,
      country: undefined,
      icon: '',
      main: undefined,
      celsius: undefined,
      temp_max: null,
      temp_min: null,
      description: "",
      error: false
    };

    this.weatherIcon = {
      Thunderstorm: <img src={thunderstorm} alt='thunderstorm'/>,
      Drizzle: <img src={drizzle} alt='drizzle'/>,
      Rain: <img src={rain} alt='rain'/>,
      Snow: <img src={snow} alt='snow'/>,
      Atmosphere: <img src={atmos} alt='atmos'/>,
      Clear:<img src={clear} alt='clear'/>,
      Clouds: <img src={clouds} alt='clouds'/>
    };
  }
 
  
  get_WeatherIcon(icons, rangeId) {
    this.setState({ icon: icons.download});
     

    switch (true) {
      case rangeId >= 200 && rangeId < 232:
        this.setState({ icon:icons.Thunderstorm});
        break;
      case rangeId >= 300 && rangeId <= 321:
        this.setState({ icon: icons.Drizzle });
        break;
      case rangeId >= 500 && rangeId <= 521:
        this.setState({ icon: icons.Rain });
        break;
      case rangeId >= 600 && rangeId <= 622:
        this.setState({ icon: icons.Snow });
          
        break;
      case rangeId >= 701 && rangeId <= 781:
        this.setState({ icon: icons.Atmosphere });
        break;
      case rangeId === 800:
        this.setState({ icon: icons.Clear });
        break;
      case rangeId >= 801 && rangeId <= 804:
        this.setState({ icon: icons.Clouds });
        break;
      default:
        this.setState({ icon: icons.Clouds });
    }
  }

  calCelsius(temp) {
    let cell = Math.floor(temp - 273.15);
    return cell;
  }

  getWeather = async e => {
    e.preventDefault();

    const country = e.target.elements.country.value;
    const city = e.target.elements.city.value;

    if (country && city) {
      const api_call = await fetch(
        `http://api.openweathermap.org/data/2.5/weather?q=${city},${country}&appid=${Api_Key}`
      );

      const response = await api_call.json();
       if(response.name && response.sys.country[0]===country[0].toUpperCase())
       {
        console.log(response.name+ " "+response.sys.country);

      this.setState({
        city: `${response.name}, ${response.sys.country}`,
        country: response.sys.country,
        main: response.weather[0].main,
        celsius: this.calCelsius(response.main.temp),
        temp_max: this.calCelsius(response.main.temp_max),
        temp_min: this.calCelsius(response.main.temp_min),
        description: response.weather[0].description,
        error: false
      });
    

      // seting icons
      this.get_WeatherIcon(this.weatherIcon, response.weather[0].id);

    }
    else {
      this.setState({
        error: true
      });
    }
   } else {
      this.setState({
        error: true
      });
    }
  };

  render() {
    // console.log(this.state.icon);
    return (
      <div className="App">  
        <Form loadweather={this.getWeather} error={this.state.error} />
        {this.state.error ? alert('Please enter correct city and country name') :
        <Weather
          cityname={this.state.city}
          weatherIcon={this.state.icon}
          temp_celsius={this.state.celsius}
          temp_max={this.state.temp_max}
          temp_min={this.state.temp_min}
          description={this.state.description}
        />}
      </div>
    );
  }
}

export default App;

